@import "../ui/common.scss";

#nav-bar {
  display: block;
  min-width: 300px;
  height: 100%;
  overflow: hidden;
  position: fixed;
  z-index: 1;
  min-height: 100vh;

  @media (max-width: $maxWidth) {
    width: 100%;
    margin-left: 0;
    min-height: 50vh;
    position: inherit;
  }

  .navbar-dark .navbar-toggler-icon {
    font-size: 50px;
  }
  .brand {
    color: white;
    font-size: xx-large;
  }

  .header-links {
    width: 100%;

    .linked-in {
      border-bottom: none;
    }
    a {
      color: white;
      border-bottom: 1px solid white;
      padding: 10px;
      transition: all ease-in-out 0.5s;
      @media (max-width: $maxWidth) {
        font-size: 2em;
      }
    }

    a:hover {
      font-size: 1.5rem;
    }

    a:focus {
      box-shadow: none;
    }
  }
}
