@import "../ui/common.scss";

#experience {
  .time-line {
    text-align: center;
    ul {
      position: relative;
      list-style: none;
      z-index: 2;
      li {
        padding-top: 16em;
        color: white;
        span {
          width: 200px;
          height: 200px;
          border-radius: 100%;
          background: #5bc0de;
          border: 9px solid white;
          padding: 31px 8px 12px 7px;
          margin-right: 30px;
          z-index: 2;
        }

        span:before {
          content: "";
          position: absolute;
          border-left: 5px solid grey;
          left: 50.3%;
          bottom: 0;
          z-index: -1;
          height: 100%;
        }
        .time-right {
          margin-left: 16em;
          margin-top: -10em;
          font-weight: bold;
          color: black;
          @media (max-width: $maxWidth) {
            font-size: 1.5em;
            margin-top: -10em;
          }
        }
        .time-left {
          margin-left: -22em;
          margin-top: -1.6em;
          font-weight: bold;
          color: black;
          @media (max-width: $maxWidth) {
            font-size: 1.5em;
            margin-left: -23em;
          }
        }

        .speech-bubble {
          margin-top: -10.5em;
          margin-left: 2.6em;
          position: relative;
          background: #00aabb;
          border-radius: 0.4em;
          width: 400px;
          height: 300px;
          @media (max-width: $maxWidth) {
            margin-left: -1em;
            margin-top: -15em;
            width: 28em;
            height: 28em;
          }
          .card {
            width: 25em;
            @media (max-width: $maxWidth) {
              width: 28em;
            }
          }
          .card-header {
            font-size: 1.2em;
            @media (max-width: $maxWidth) {
              font-size: 1.8em;
            }
          }
          .card-text {
            font-size: 17px;
            @media (max-width: $maxWidth) {
              font-size: 23px;
            }
          }
        }

        .speech-bubble:after {
          content: "";
          position: absolute;
          right: 0;
          top: 50%;
          width: 0;
          height: 0;
          border: 1.094em solid transparent;
          border-left-color: #00aabb;
          border-right: 0;
          margin-top: -1.094em;
          margin-right: -1.094em;
        }

        .speech-bubble-right {
          margin-top: -10.5em;
          margin-left: 39.7em;
          position: relative;
          background: #00aabb;
          border-radius: 0.4em;
          width: 400px;
          height: 300px;

          @media (max-width: $maxWidth) {
            margin-top: -15em;
            width: 28em;
            height: 28em;
          }
          .card {
            width: 25em;
            @media (max-width: $maxWidth) {
              width: 28em;
            }
          }
          .card-header {
            font-size: 1.2em;
            @media (max-width: $maxWidth) {
              font-size: 1.8em;
            }
          }
          .card-text {
            font-size: 17px;
            @media (max-width: $maxWidth) {
              font-size: 23px;
            }
          }
        }

        .speech-bubble-right:after {
          content: "";
          position: absolute;
          left: 0;
          top: 50%;
          width: 0;
          height: 0;
          border: 20px solid transparent;
          border-right-color: #00aabb;
          border-left: 0;
          margin-top: -20px;
          margin-left: -20px;
        }
      }
    }
  }
}
