@import "../ui/common.scss";

#projects {
  .animated {
    height: 100%;
  }
  .card-title {
    font-size: 1.5rem;
    @media (max-width: $maxWidth) {
      font-size: 2.2rem;
    }
  }
  .card-text {
    font-size: 1rem;
    @media (max-width: $maxWidth) {
      font-size: 1.5rem;
    }
  }
  .card {
    box-shadow: 4px 23px 40px grey;
  }
}
