html{
  scroll-behavior: smooth;
}
body {
  scroll-behavior: smooth;
  margin: 0;
  font-family: cursive, serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  zoom: 0%;
}
#root {
  display: block;
}

@media (max-width: 1024px) {
  #root {
    display: block;
  }
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
