@import "./ui/common.scss";

#info-container {
  font-family: proxima-nova;// "Raleway";//cursive, serif;
  color: rgb(66, 65, 65);
  position: relative;
  margin-left: 300px;
  padding-top: 20px;
  // background-color: #d3d3d3;

  @media (max-width: $maxWidth) {
    width: 100%;
    margin-left: 0;
    min-height: 50vh;
  }
  .header {
    height: 55px;
    width: 100%;
    font-size: 1.3rem;
    font-weight: bold;
    background-color: #13215f;
    color: white;
    // background: linear-gradient(to right, #5bc0de, white);
    // opacity: 0.8;

    @media (max-width: $maxWidth) {
      height: 2.5em;
      font-size: 2em;
    }
  }
}
