@import "../ui/common.scss";

#home {
  h1 {
    .name-highlight {
      color: #04c2c9;
    }
    @media (max-width: $maxWidth) {
      font-size: 4.5em;
    }
  }

  h3 {
    @media (max-width: $maxWidth) {
      font-size: 2.5em;
    }
  }

  .indroduction {
    font-size: 1.3rem;
    line-height: 2.5rem;

    @media (max-width: $maxWidth) {
      font-size: 2.5em;
      line-height: 4rem;
    }
  }
}
